<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div>
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>

            <div class="row placeholder">
                <div class="col-md-12 p-0">

                    <ReportViewer v-if="parameter.length > 0" :parameter="parameter" :reportPath="reportPath"
                        :isLoading="isLoading">
                    </ReportViewer>
                </div>
            </div>
        </div>
    </section>
</template>



<script>
import DataService from "../../service/DataService";
import ReportViewer from './ReportViewer.vue';


export default {
    components: {
        ReportViewer
    },
    data() {
        return {
            isLoading: false,
            reportPath: null,
            parameter: [],
            FormSchema: [{
                layoutType: "double",
                Data: [{
                    astype: "SelectField",
                    label: this.$t("ApprovalType"),
                    name: "ApprovalType",
                    value: '',
                    config: {
                        options: [],
                    },
                },
                {
                    astype: "TextField",
                    label: this.$t("UserName"),
                    name: "UserName",
                    value: "",
                    placeholder: "Search by User Name",
                    config: {
                        options: [],
                    },
                },
                ],
            },],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-search pr-2'></i>Search",
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Clear all",
                onClick: this.ClearAll
            },
            ],
        };
    },
    created() {
        var vm = this;
        this.GetApprovalTypes();
    },
    methods: {
        async GetApprovalTypes() {
            var vm = this;
            var userTypeID = null;
            var userType = vm.GetUserInfo.UserType;
            if (userType == 'CA') {
                userTypeID = 1;
            } else if (userType == 'PM') {
                userTypeID = 2;
            } else if (userType == 'NU') {
                userTypeID = 3;
            }
            var url = `userTypeID=${userTypeID}`;
            await DataService.GetApprovalTypes(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.APPROVAL_TYPE_ID,
                                name: item.APPROVAL_TYPE_NAME
                            })
                        })
                    }
                }
            });
        },
        async onSubmit(e) {
            var vm = this;
            vm.isLoading = true;
            vm.reportPath = '';
            vm.parameter = [];
            let myObj = {
                reportName: "nonruleuserreport",
                ApprovalTypeIds: e.ApprovalType,
                UserName: e.UserName,
            }
            await DataService.ReportMain(myObj).then(function (response) {
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
                vm.isLoading = false;
            });

        },
        ClearAll: function () {
            var vm = this;
            (vm.FormSchema)[0].Data[0].value = '';
            (vm.FormSchema)[0].Data[1].value = '';
            vm.parameter = [];
        },
    },
};
</script>

<style>
.vue-treeselect__control {
    border: 1px solid #100e0e6b !important;
}

.input-group .w-100 .d-block {
    position: absolute;
    margin-top: 37px;
    margin-left: 10px;
}
</style>
