<template lang="en">
<section>
    <loader :is-visible="isLoading"></loader>
    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>

    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath" :isLoading="isLoading"></ReportViewer>
        </div>
    </div>
</section>
</template>

<script>
import ReportViewer from "./ReportViewer.vue";
import DataService from "../../service/DataService";
export default {
    components: {
        ReportViewer
    },
    data() {
        return {
            isLoading: false,
            reportPath: null,
            parameter: [],
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                    astype: "TextField",
                    label: this.$t("RuleName"),
                    name: "RuleName",
                    value: null,
                    placeholder: "",
                    config: {
                        options: [],
                    },
                    validationRules: {
                        required: false,
                        max: 250
                    },
                },
                {
                    astype: "MultiSelectField",
                    label: this.$t("Module"),
                    name: "Module",
                    value: "",
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                        //onChange: this.onChangeDropdown,
                    },
                },
                {
                    astype: "MultiSelectField",
                    label: this.$t("ApprovalChain"),
                    name: "ApprovalChain",
                    value: "",
                    placeholder: "",
                    mode: "tag",
                    config: {
                        options: [],
                    },
                },
                {
                    astype: "MultiSelectField",
                    label: this.$t("User"),
                    name: "User",
                    value: "",
                    placeholder: "",
                    mode: "tag",
                    config: {
                        options: [],
                    },
                },
                ],
            },],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-search pr-2'></i>Search",
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Clear all",
                onClick: this.ClearAll
            },
            ],
        }
    },
    created() {
        var vm = this;
        this.GetApprovalTypes();
        this.GetApprovalChainsByCompanyId();
        this.LoadUsersByCompanyId();
    },
    methods: {
        async GetApprovalTypes() {
            var vm = this;
            var userTypeID = null;
            var userType = vm.GetUserInfo.UserType;
            if (userType == 'CA') {
                userTypeID = 1;
            } else if (userType == 'PM') {
                userTypeID = 2;
            } else if (userType == 'NU') {
                userTypeID = 3;
            }
            var url = `userTypeID=${userTypeID}`;
            await DataService.GetApprovalTypes(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.APPROVAL_TYPE_ID,
                                name: item.APPROVAL_TYPE_NAME
                            })
                        })
                    }
                }
            });
        },
        async GetApprovalChainsByCompanyId() {
            var vm = this;
            await DataService.GetApprovalChainsByCompanyId().then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.FormSchema[0].Data[2].config.options.push({
                                value: item.APPROVAL_CHAIN_ID,
                                name: item.APPROVAL_CHAIN_NAME
                            })
                        })
                    }
                }
            });
        },
        async LoadUsersByCompanyId() {
            var vm = this;
            var url = `isIncludeLoginUserId=1&approvalGroupIds=&approvalChainIds=&moduleName=${'APPROVALRULE'}&DepartmentIds`;
            await DataService.LoadUsersByCompanyId(url).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.FormSchema[0].Data[3].config.options.push({
                                value: item.USER_ID,
                                name: item.USERNAME
                            })
                        })
                    }
                }
            });
        },
        async onSubmit(e) {
            var vm = this;
            vm.isLoading = true;
            vm.reportPath = '';
            vm.parameter = [];

            var moduleIds = "";
            if (e.Module.length > 0) {
                e.Module.forEach((item, index) => {
                    if (index == 0) {
                        moduleIds = item.value;
                    } else {
                        moduleIds += ',' + item.value;
                    }
                })
            }

            var chainIds = "";
            if (e.ApprovalChain.length > 0) {
                e.ApprovalChain.forEach((item, index) => {
                    if (index == 0) {
                        chainIds = item.value;
                    } else {
                        chainIds += ',' + item.value;
                    }
                })
            }

            var userIds = "";
            if (e.User.length > 0) {
                e.User.forEach((item, index) => {
                    if (index == 0) {
                        userIds = item.value
                    } else {
                        userIds += ',' + item.value
                    }
                })
            }
            let myObj = {
                reportName: "userwiseapprovalruleinformation",
                ruleName: e.RuleName,
                moduleIds: moduleIds,
                approvalChainIds: chainIds,
                Idsuser: userIds,
            }
            await DataService.ReportMain(myObj).then(function (response) {
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
                vm.isLoading = false;
            })
        },
        ClearAll: function () {
            var vm = this;
            (vm.FormSchema)[0].Data[0].value = '';
            (vm.FormSchema)[0].Data[1].value = '';
            (vm.FormSchema)[0].Data[2].value = '';
            (vm.FormSchema)[0].Data[3].value = '';
            vm.parameter = [];
        },
    }
}
</script>
